.content-wrapper{
  display: flex;
  .wrapper();
  margin-top: 35px;
  .xs-block({
    margin-top: 20px;
  });

  .main-wrapper{
    .col();
    .size(9.8);
    .size-md(12);

    .md-block({
      order: 2;
    })
  }
  .aside{
    .col();
    .size(2.2);
    .size-md(12);

    .md-block({
      order: 1;
    })
  }
}
.aside-banner{
  .col();
  .size(12);
}
//md
.content-wrapper{
  .md-block({
    flex-direction: column;
    align-items: center;
  })
}
.aside-banner{
  .md-block({
    display: flex;
    justify-content: center;
    margin-top: 30px;
  })
}
