.slide{
  background-color: @col_back;
  padding-top: 38px;

  .md-block({
    padding-top: 28px;
  });
  .sm-block({
    padding-top: 20px;
  });
  .xs-block({
    padding-top: 15px;
  });

  &-title{
    .h2();
  }
}
// A D
.slide-ad{
  position: relative;
  text-align: center;
  .col();
  margin-left: 0;
  margin-right: 0;
  width: 100%;
  margin-bottom: 30px;
  padding: 29px 0;
  border-top: 1px solid @col_line;
  border-bottom: 1px solid @col_line;
}
.slide-ad{
  .sm-block({
    margin-bottom: 20px;
    padding: 18px 0;
  })
}
.slide-ad:hover{
  cursor: pointer;
}
//      * S L I D E R *

.slide__slick-carousel{
  overflow: hidden;
}

