// title
.h2(){
  font-family: 'Roboto-Bold';
  font-size: 20px;
  color: @col_header;
  margin-left: 34px;
  margin-bottom: 18px;

  .xs-block({
    font-size: 18px;
    margin-left: 24px;
    margin-bottom: 12px;
  })
}
.h3(){
  font-family: 'Roboto-Medium';
  font-size: 14px;
  color: #000;
  text-transform: uppercase;

  .md-block({
    font-size: 13px;
  })
}
.h4(){
  font-family: 'Roboto-Medium';
  font-size: 17px;
  color: #000;
  padding-left: 25px;
  padding-bottom: 15px;

  .xs-block({
    font-size: 16px;
