.social-media{
  .row-flex();
  align-items: center;
  justify-content: center;
  padding-top: 33px;
  padding-bottom: 50px;

  .sm-block({
    padding-top: 22px;
    padding-bottom: 35px;
  });
  .xs-block({
    padding-top: 15px;
    padding-bottom: 20px;
  });

  &__text{
    text-transform: uppercase;
    .f20();
    margin-left: 15px;

    .sm-block({
      .f18();
      margin-bottom: 15px;
    });
    .xs-block({
      .f17();
      margin-bottom: 12px;
    })
  }
  &__icon{
    margin-left: 15px;
  }
}
.social:not(:last-child){
  margin-right: 20px;
}



// media
@media screen and (max-width: 1120px){

  .social-media__text{
    margin-bottom: 20px;
  }
}