.footer{
  .wrapper();
  display: flex;
  flex-direction: column;
  background-color: @col_header;
  color: @col_head_font;
  padding-top: 15px;
  padding-bottom: 80px;

  .md-block({
    padding-bottom: 80px;
  });
  .sm-block({
    padding-bottom: 50px;
  });
  .xs-block({
    padding-bottom: 30px;
  })
}
// menu list
.footer-menu{
  &__list{
    display: flex;
    padding-bottom: 15px;

    .sm-block({
      justify-content: center;
      flex-wrap: wrap;
    })
  }
  &__item{
    .f12();
    padding-right: 4px;

    .sm-block({
      padding-bottom: 3px;
    })
  }
  &__item:not(:last-child){
    border-right: 1px solid;
    margin-right: 4px;
  }
  &__item:hover,
  &__item:active{
    color: @col_text;
  }
}
// main
.footer-main{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  .f12();

  .md-block({
    flex-wrap: wrap;
    justify-content: space-around;
  });

  &__img{
    .md-block({
      order: 3;
      margin-top: 30px;
    });
    .xs-block({
      order: 2;
      margin-top: 20px;
    })
  }
  &__copyright{
    .f12();
    .size(5.5);
    .size-md(8);
    .size-xs(12);

    .md-block({
      order: 4;
      margin-top: 30px;
    });
    .xs-block({
      margin-top: 20px;
    })
  }
  &__title{
    font-family: 'Roboto-LightItalic';
    .f16();
    text-align: center;
    .size(3.5);
    .size-md(8);
    .size-sm(7.8);
    .size-xs(11);

    .md-block({
      order: 1;
    })
  }
  &__info{
    text-align: center;
    padding: 4px 10px 5px 10px;
    border: 2px solid @col_back;
    .size(2.4);
    .size-md(4);
    .size-sm(3.8);
    .size-xs(6);

    .md-block({
      order: 2;
    });
    .xs-block({
      order: 3;
      margin-top: 20px;
    })
  }
}

// breadcrumbs
.footer-head__breadcrumbs{
  font-size: 12px;
  margin-bottom: 10px;

  .sm-block({
    font-size: 11px;
  });
  .xs-block({
    font-size: 10px;
    margin-bottom: 8px;
  })
}
.breadcrumbs{
  overflow: hidden;
  width: 100%;
}

.breadcrumbs li{
  float: left;
  margin: 0 .5em 0 1em;
}

.breadcrumbs a{
  background: #ddd;
  padding: .3em .2em;
  float: left;
  text-decoration: none;
  color: #444;
  text-shadow: 0 1px 0 rgba(255,255,255,.5);
  position: relative;
}

.breadcrumbs a:hover{
  background: #99db76;
}

.breadcrumbs a::before{
  content: "";
  position: absolute;
  top: 50%;
  margin-top: -1.5em;
  border-width: 1.5em 0 1.5em 1em;
  border-style: solid;
  border-color: #ddd #ddd #ddd transparent;
  left: -1em;
}

.breadcrumbs a:hover::before{
  border-color: #99db76 #99db76 #99db76 transparent;
}

.breadcrumbs a::after{
  content: "";
  position: absolute;
  top: 50%;
  margin-top: -1.5em;
  border-top: 1.5em solid transparent;
  border-bottom: 1.5em solid transparent;
  border-left: 1em solid #ddd;
  right: -1em;
}

.breadcrumbs a:hover::after{
  border-left-color: #99db76;
}

.breadcrumbs .current,
.breadcrumbs .current:hover{
  font-weight: bold;
  background: none;
}

.breadcrumbs .current::after,
.breadcrumbs .current::before{
  content: normal;
}

