.reviews{
  background-color: @col_back;
  padding: 34px 0;

  .xs-block({
    padding: 22px 0;
  });
  &-title{
    .h2();

    .sm-block({
      font-size: 18px;
    })
  }
}
.reviews{
  &-items{
    display: flex;
    justify-content: center;
    margin-top: 22px;
    .row-flex();
  }
  &-item{
    .size(6);
    .size-xs(12);
  }
}
// item
.reviews-item{
  margin-bottom: 50px;

  .md-block({
    margin-bottom: 40px;
  });
  .sm-block({
    margin-bottom: 30px;
  });
  .xs-block({
    margin-bottom: 20px;
  });

  &__block{
    .row-flex();
  }
  &__img{
    .col();
    .size(2.5);
    .size-xs(1.8);
    text-align: center;
    padding-top: 15px;

    .xs-block({
      margin-right: 0;
      margin-left: 10px;
    })
  }
  &__wrapper{
    .col();
    .size(9);
    .size-xs(10.4);

    .xs-block({
      padding-left: 25px;
    })
  }
}
// img
.img-item{
  padding: 16px 14px 18px 16px;
  background-color: @col_head_font;
  border-radius: 50%;

  .sm-block({
    .i-person{
      width: 25px;
      height: 30px;
    }
  });
  .xs-block({
    padding: 12px 11px 14px 12px;
    .i-person{
      width: 20px;
      height: 25px;
    }
  })
}
// text
.reviews-item{
  &__wrapper{
    font-family: 'Roboto-LightItalic';
    .f13_26();
    color: @col_text;
  }
  &__author{
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
  }
}
.author{
  font-family: 'Roboto-MediumItalic';
  color: #000;
}
// reviews-more
.reviews-more{
  padding-top: 0;
}