.scrollup{
  display: flex;
  align-items: center;
  padding: 15px;
  opacity: .6;
  border-radius: 5px;
  background: @col_header;
  position:fixed;
  bottom: 120px;
  right:10px;
  display:none;

  .md-block({
    padding: 14px;
  });
  .sm-block({
    padding: 13px;
  });
  .xs-block({
    padding: 10px;
  })
}