.icon{
  fill: #000;
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  font-size: 25px;
}
.icon:hover{
  cursor: pointer;
}
// user
.i-user{
  fill: #fff;
  width: 12px;
  height: 14px;
  margin-right: 5px;
}
// menu
.i-menu{
  width: 21px;
  height: 19px;
}
// place
.i-place{
  width: 16px;
  height: 21px;
}
// loop
.i-loop{
  fill: #fff;
  width: 19px;
  height: 19px;
}
// personal
.i-person{
  fill: #fff;
  width: 29px;
  height: 34px;
}
// social media
.social{
  font-size: 50px;
}
.social:hover,
.social:active{
  fill: @col_text;
}
// scroll up
.icon-up{
  fill: #fff;
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  font-size: 25px;
}