.news{
  background-color: @col_back;
  padding-top: 38px;
  font-family: 'Roboto-Medium';

  .md-block({
    padding-top: 26px;
  });

  &-title{
    .h2();
  }
  &-items{
    .row-flex();
    justify-content: center;
  }
  &-item{
    .col();
    .size(4);
    .size-md(5);
    .size-sm(6);
    .size-xs(10);
    .size-xxs(11);
    display: flex;
    flex-direction: column;
  }
}
// item
.news-item{
  position: relative;
  margin-bottom: 30px;

  .xs-block({
    margin-bottom: 15px;
  });

  &__content{
    padding-top: 12px;

    .xs-block({
      padding-top: 5px;
    });
  }
  &__title{
    .h3();
    margin-bottom: 9px;

    .xs-block({
      margin-bottom: 4px;
    });
  }
  &__date{
    font-family: 'Roboto-MediumItalic';
    .f12();

    .sm-block({
      .f11();
    })
  }
  &__text{
    color: @col_head_logo;
  }
}
// image
.news-image{
  height: 235px;
  width: 272px;
  object-fit: contain;
  object-fit: scale-down;
}
// action
.action-bord{
  border: 3px solid @col_head_logo;
  .action-back{
    background: @col_head_logo;
    color: #fff;
  }
  .news-item__title,
  .news-item__text{
    color: #fff;
  }
}
.action{
  position: absolute;
  top: 20px;
  left: 35%;
  text-transform: uppercase;
  .f17();
  color: @col_head_logo;
  padding: 7px 12px 5px 12px;
  border: 1px solid;
  border-radius: 20px;

  .md-block({
    .f16();
  });
  .sm-block({
    padding: 5px 10px;
    border-radius: 16px;
  });
  .xs-block({
    .f15();
  })
}
.action:hover,
.action:active{
  color: #fff;
  background: @col_head_logo;
  border: 2px dotted @col_head_logo;
}
//    -   -   -
.news-more{
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  padding-top: 90px;

  .md-block({
    padding-top: 70px;
  });
  .sm-block({
    padding-top: 40px;
  });
  .xs-block({
    padding-top: 20px;
  })
}
.more-link{
  border-bottom: 1px dotted;
}
.more-link:hover{
  cursor: pointer;
}