.new_clinics{
  position: relative;
  background-color: @col_back;
  padding: 34px 0;

  .xs-block({
    padding: 22px 0;
  });
  &-title{
    .h2();

    .sm-block({
      font-size: 18px;
    })
  }
}
.new_clinics::after{
  position: absolute;
  content: '';
  bottom: 0;
  left: 0;
  height: 1px;
  width: 100%;
  background-color: @col_body;
}
.new_clinics{
  &-items{
    display: flex;
    flex-direction: column;

    .xs-block({
      margin-top: 15px;
    })
  }
  &-item{
    .row-flex();
  }
  &-item__image{
    .col();
    .size(2);
    .size-xs(4);
  }
  &-item__info{
    .col();
    .size(10);
    .size-xs(12);
  }
}
//info
.info-block{
  display: flex;
  justify-content: space-between;
  .f12();

  &__review{
    display: flex;

    .review{
      border-bottom: 1px dotted;
      margin-right: 10px;
    }
  }
}
// stars
.review-rating{

  &__group{
    position: relative;
    font-size: 6px;
    margin-right: 10px;
    width: 10em;
    height: 2em;
    background-image: url("../img/svg/icons/star-dis.svg");
    background-size: 2em 1.9em;
    background-repeat: repeat-x;
  }
  &__input{
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
  }
  &__stars{
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    height: 2em;
    background-size: 2em 1.9em;
    background-repeat: repeat-x;
  }
  &__stars:hover,
  &__input:checked + &__stars{
    background-image: url("../img/svg/icons/star-act.svg");
    cursor: pointer;
  }
  &__stars:hover ~ &__stars{
    background-image: url("../img/svg/icons/star-dis.svg");
    cursor: pointer;
  }
  // options
  &__stars:nth-of-type(1) {
    z-index: 5;
    width: 2em;
  }

  &__stars:nth-of-type(2) {
    z-index: 4;
    width: 4em;
  }

  &__stars:nth-of-type(3) {
    z-index: 3;
    width: 6em;
  }

  &__stars:nth-of-type(4) {
    z-index: 2;
    width: 8em;
  }

  &__stars:nth-of-type(5) {
    z-index: 1;
    width: 10em;
  }
}
//
.info-gallery{
  padding: 20px 0;

  .xs-block({
    padding: 10px 0;
    text-align: center;
  });
  .gallery-image{
  }
}
.info-contacts{
  .list-item{
    .f14();
    color: @col_grey;

    .xs-block({
      .f12();
    })
  }
}
.allotment{
  font-family: 'Roboto-Bold';
  .f14();
  color: @col_grey;
}
.info-footer{
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 16px 0;

  .xs-block({
    padding: 10px 0;
  });
  .xxs-block({
    justify-content: space-around;
  });
  &__review{
    .f12();
    border-bottom: 1px dotted;

    .xs-block({
      .f11();
    })
  }
  &__review:hover{
    cursor: pointer;
  }
}
// info button
.info-footer__button {
  display: flex;

  .xxs-block({
    flex-direction: column;
  });
}
.button{
  text-align: center;
  width: 159px;
  padding-top: 9px;
  padding-bottom: 10px;
  border: 1px solid @col_pink_btn;
  border-radius: 30px;
  color: @col_pink_btn;
  background-color: @col_back;

  .md-block({
    width: 140px;
    padding-top: 8px;
    padding-bottom: 9px;
    .f15();
  });
  .xs-block({
    width: 120px;
    padding-top: 6px;
    padding-bottom: 7px;
    .f14();
  });
  .xxs-block({
    width: 100px;
    padding-top: 6px;
    padding-bottom: 6px;
    .f13();
  });
  &-sign_up{
    margin-right: 23px;

    .sm-block({
      margin-right: 15px;
    });
    .xs-block({
      margin-right: 8px;
    });
    .xxs-block({
      margin-bottom: 5px;
      margin-right: 5px;
    })
  }
}
.button:hover,
.button:active{
  color: @col_back;
  background-color: @col_pink_btn;
  cursor: pointer;
}
// button
.add-button{
  .col();
  width: 194px;
  position: absolute;
  top: 19px;
  right: 0;
}

.add-button{
  display: flex;
  justify-content: center;
  color: #fff;
  border-radius: 20px;
  background-color: @col_head_logo_tx;

  .md-block({
    width: 170px;
    justify-content: flex-end;
    right: -15px;
  });
  .sm-block({
    width: 160px;
    border-radius: 18px;
  });
  .xs-block({
    position: relative;
    top: 0;
    right: 0;
    left: calc(50% - 80px);
  });
  .xxs-block({

  })
}
.add-button:hover{
  cursor: pointer;
}
.add-button::before,
.add-button::after{
  content: '';
  position: absolute;
  top: 22px;
  left: 10px;
  width: 16px;
  height: 1px;
  background-color: #fff;

  .md-block({
    left: 13px;
    width: 15px;
  });
  .sm-block({
    left: 8px;
    top: 20px;
  });
  .xs-block({
    width: 14px;
    top: 18px;
  })
}
.add-button::after{
  transform: rotate(90deg);
}
.add-button__link{
  display: block;
  padding: 13px 0;

  .md-block({
    margin-right: 10px;
  });
  .sm-block({
    padding: 11px 0;
    margin-right: 6px;
    font-size: 15px;
  });
  .xs-block({
    font-size: 14px;
  })
}
// more
.items-more{
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  padding-top: 26px;

  .md-block({
    padding-top: 20px;
  });
  .sm-block({
    padding-top: 15px;
  });
  .xs-block({
    padding-top: 10px;
  })
}
.more-link:hover{
  cursor: pointer;
}

