.header{
  .wrapper();
}
.header-top{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  background-color: @col_header;
  .f12();
  color: @col_head_font;
  padding: 9px 0;

  &__wrapper{
    align-items: center;
    justify-content: space-between;

    .md-block({
      flex-direction: column;
    })
  }
  &__phone {
    padding: 5px 8px 4px 8px;
    border: 1px solid #fff;
    margin-right: 50px;

    .phone-link{
      font-family: 'Roboto-Black';
    }
  }
  &__language{
    color: #fff;
    margin-right: 42px;

    .language-list__item:not(:last-child){
      border-right: 1px solid;
      margin-right: 4px;
      padding-right: 4px;
    }
  }
  &__enter{
    color: #fff;
  }
}
.header-top{
  .sm-block({
    flex-direction: column;
  })
}
.header-top__phone{
  .sm-block({
    margin-top: 5px;
  })
}
.header-top__phone .phone-link{
  .xxs-block({
    font-size: 10px;
  })
}
.wrapper-personal{
  display: flex;

  .md-block({
    justify-content: space-around;
    align-self: stretch;
    margin-top: 5px;
  })
}
.header-top__phone,
.header-top__language{
  .md-block({
    margin-right: 0;
  })
}
.language-list__item:hover,
.header-top__phone:hover{
  color: @col_text;
}

.menu-list,
.header-top__wrapper,
.header-top__enter,
.language-list{
  display: flex;
}
.menu-list{

  &__item{
    margin-right: 5px;
    padding-right: 5px;
  }
  &__item:hover,
  &__item:active{
    color: @col_grey;
  }
  &__item:not(:last-child){
    border-right: 1px solid #fff;
  }
}
// top-dotted
.language-list__item,
.enter-text{
  border-bottom: 1px dotted @col_head_font;
}

.header-main{
  .row-flex();
  padding: 35px 0;
  align-items: center;
  justify-content: center;

  .md-block({
    flex-direction: column;
    justify-content: center;
    padding: 20px 0;
  });
  .xs-block({
    padding: 10px 0;
  });

  &__logo-wrapper{
    .col();
    .size(5);
    .size-md(7.5);
    .size-sm(9);
    .size-xs(10.5);
    .size-xxs(11);

    .md-block({
      margin-bottom: 20px;
    });
    .xs-block({
      margin-bottom: 10px;
    })
  }
  &__ad{
    .col();
    padding-top: 15px;
    .size-xs(11);
    .size-xxs(12);

    .md-block({
      padding-top: 5px;
    });
    .sm-block({
      padding-top: 0;
    })
  }
  // logo
  &__logo{
    display: flex;
    align-items: center;

    .logo-text{
      font-family: 'Roboto-Black';
      color: @col_head_logo;
      margin-left: 25px;

      .xs-block({
        margin-left: 10px;
      })
    }
  }
  &__review{
    margin-left: 35px;
    padding-top: 20px;

    .md-block({
      margin-left: 10px;
    });

    .review-value{
      font-family: 'Roboto-Black';
      color: @col_header;
    }
    .color{
      color: @col_head_logo_tx;
    }
  }
}
.header-main__ad:hover{
  cursor: pointer;
}
.header-main__logo{
  .xxs-block({
    flex-direction: column;
  })
}
.header-main__logo .logo-text{
  .xxs-block({
    margin-left: 30px;
    margin-top: 15px;
  })
}
.header-main__review{
  .xxs-block({
    margin-left: 12px;
    padding-top: 8px;
  })
}
.header-main__logo-wrapper{
  .xxs-block({
    margin-bottom: 10px;
  })
}
.header-main__review .review-value{
  .xxs-block({
    font-size: 14px;
  })
}

//      * S  E  A  R  C  H  *

.search{
  position: relative;

  &-wrapper{
    .row-flex();
    position: relative;

    .md-block({
      justify-content: center;
      width: 100%;
      margin-bottom: 25px;
    });
    .xs-block({
      margin-bottom: 15px;
    })

  }
  &-form{
    .col();
    .size(9);
    .size-md(11);
    .size-sm(11);
    .size-xs(12);
    position: relative;
    z-index: 999999;
  }
  &-btn{
    .col();
    width: 194px;
    position: absolute;
    top: -3px;
    right: 0;
  }
}
.search {
  .md-block({
    position: static;
    display: flex;
    flex-direction: column;
    align-items: center;
  })
}
.i-menu{
  position: absolute;
  top: 10px;
  left: 14px;
}
.i-place{
  position: absolute;
  top: 7px;
  left: 13px;

  .md-block({
    left: 2px;
  });
  .xs-block({
    left: 0;
  })
}
.search-form__input{
  width: 100%;
  border: 2px solid @col_search;
  border-radius: 20px;
  height: 40px;
  padding-left: 50px;
}
.search-form__button{
  position: absolute;
  top: 2px;
  right: 54px;
  width: 130px;
  height: 36px;
  background-color: @col_search_btn;
}
// loop
.search-form__button-loop{
  position: absolute;
  top: 0;
  right: 0;
  width: 54px;
  height: 40px;
  background-color: @col_search;
  border-radius: 0 20px 20px 0;
}
.search-form__button-loop:hover{
  .i-loop{
    cursor: pointer;
    fill: @col_grey;
  }
}
// button
.search-btn{
  display: flex;
  justify-content: center;
  color: #fff;
  border-radius: 20px;
  background-color: @col_head_logo_tx;

  .md-block({
    position: relative;
    width: 220px;
  });
  .xs-block({
    width: 180px;
    justify-content: flex-end;
  });
  .xxs-block({
    width: 160px;
  })
}
.search-btn:hover{
  cursor: pointer;
}
.search-btn::before,
.search-btn::after{
  content: '';
  position: absolute;
  top: 22px;
  left: 10px;
  width: 16px;
  height: 1px;
  background-color: #fff;

  .md-block({
    left: 13px;
    width: 15px;
  });
  .sm-block({
    left: 10px;
  });
  .xs-block({
    top: 22px;
  })
}
.search-btn::after{
  transform: rotate(90deg);
}
.search-btn__button{
  display: block;
  padding: 13px 0;

  .xs-block({
    padding-right: 15px;
  })
}
.search-form__button{
  .md-block({
    width: 100px;
  });
  .xs-block({
    width: 80px;
    font-size: 14px;
    right: 44px;
  })
}

// services list
.search-services{
  display: none;
}
.search-services {
  position: absolute;
  top: 40px;
  left: 15px;
  z-index: 999;
  background-color: @col_back;
  .row-offsets();
  .size(10);
  .size-sm(11);
  border: 2px solid #eee;
  border-radius: 5px;
  padding: 15px 0;

  .md-block({
    left: 70px;;
  });
  .sm-block({
    left: 50px;
  });
  .xs-block({
    left: 40px;
  })
}
.services{
  &-title{
    .h4();

    .sm-block({
      font-size: 15px;
      padding-left: 15px;
      padding-bottom: 10px;
    });
    .xs-block({
      font-size: 14px;
      padding-left: 10px;
      padding-bottom: 5px;
    })
  }

  &-list {
    .row-flex();
    text-transform: uppercase;
    .f13();
    margin-left: 5px;

    .md-block({
      .f12();
    });
    .xs-block({
      .f11();
    })
  }

  &-item {
    .col();
    .size(3);
    .size-sm(4);
    .size-xs(6);
    text-decoration: underline;
    padding-bottom: 4px;
    margin-bottom: 4px;

    .xs-block({
      padding-bottom: 4px;
      margin-bottom: 4px;
    })
  }
  &-item:hover,
  &-item:active{
    color: @col_pink_btn;
    text-decoration: none;
  }
}


// towns list
.search__towns{
  display: none;
}
.search__towns{
  position: absolute;
  top: 40px;
  right: 32%;
  z-index: 9990;
  background-color: @col_back;
  .row-offsets();
  .size(2);
  .size-md(3);
  .size-sm(3);
  .size-xs(3.5);
  border: 2px solid #eee;
  border-radius: 5px;
  padding: 18px 0 15px 0;

  .md-block({
    right: 13%;
  });
  .sm-block({
    right: 18%;
  });
  .xs-block({
    right: 18%;
  })
}
.towns-list{
  text-align: center;
  .f17();
  margin-left: 10px;

  &__item{
    text-decoration: underline;
    padding-top: 3px;
    padding-bottom: 3px;
    .md-block({
      .f16();
    });
    .xs-block({
      .f15();
    })
  }
  &__item:hover,
  &__item:active{
    color: @col_pink_btn;
    text-decoration: none;
  }
}
// burger
.burger-btn:hover{
  cursor: pointer;
  .burger{
    fill: #d9bf8f;
  }
}
// burger show
.view{
  display: block !important;
  transition: 3s;
}



// media
@media screen and (max-width: 410px){

  .search__towns{
    right: 20%;
    width: calc(33% - 30px);
    padding-bottom: 10px;
  }
  .towns-list__item{
    .f14();
  }

}

@media screen and (max-width: 375px) {

  .search__towns{
    width: calc(38% - 30px);
  }
}
