.slick-slide{
  display: flex;
  margin-right: 25px;

  .md-block({
    margin-right: 15px;
  });
  .sm-block({
    margin-right: 10px;
  })
}
.slick-slide {
  display: none;
  float: left;

  height: 100px;
  min-height: 60px;
  width: 100%;

  .xs-block({
    margin-left: 10px;
    height: 80px;
  })
}
.slick-initialized .slick-slide{
  display: flex;
}
.slick-list.dragging{
  cursor: pointer;
}
.slick-vertical .slick-slide{
  border: none;
}
.slick-active{
  outline: none;
}
.slick-arrow{
  display: none;
}
