.category{
  background-color: @col_back;
  padding: 40px 0;
  border-top: 1px solid @col_line;

  .xs-block({
    padding: 20px 0;
  });
  &-title{
    .h2();

    .sm-block({
      font-size: 18px;
    })
  }
}
.category-items{

  &__list{
    .row-flex();
    margin-left: @offset;
    text-transform: uppercase;
    .f14();

    .sm-block({
      margin-left: 15px;
    });
    .xs-block({
      .f13();
    });

    .list-item{
      padding-top: 10px;
      .size(4);
      .size-md(6);
      .size-xs(11);

      .xs-block({
        padding-top: 6px;;
      });
    }
  }
}
// list-link
.list-item__link{
  border-bottom: 1px dotted;
  padding-top: 5px;

  .xs-block({
    padding-top: 2px;
  });
}
.list-item__link:hover,
.list-item__link:active{
  color: @col_grey;
}
// more
.category-more{
  padding-top: 45px;

  .md-block({
    padding-top: 30px;
  });
  .sm-block({
    padding-top: 20px;
  });
  .xs-block({
    padding-top: 15px;
  })
}
.list-item__hidden{
  display: none;
}